<template>
  <div style="padding-bottom:20px;background:rgb(247, 247, 247)">
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">事件上报</span>
    </mu-appbar>-->
    <van-nav-bar
      :fixed="true"
      title="事件上报"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div style="padding:10px;margin-top:-18px">
      <div class="containerMain contanerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>监控站点：</span>
          </div>
          <div class="select" @click="selectDataPicStation" id="classPicstation">
            <!-- <select name v-model="input.valueAddress">
              <option :value="item" v-for="(item,index) in citys" :key="index">{{item}}</option>
            </select>-->
            <input
              type="text"
              disabled
              class="input"
              v-model="input.valueAddress"
              placeholder="请选择当前站点"
            />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>时间：</span>
          </div>
          <div class="select" @click="timeSelect">
            <input
              type="text"
              class="input"
              v-model="value3"
              style="background:white"
              disabled
              placeholder="选择录入的时间"
            />
          </div>
        </div>
      </div>
      <div class="containerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>事件名称：</span>
          </div>
          <div class="select">
            <input type="text" placeholder="请输入事件名称" class="input" v-model="input.valueEventName" />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>事件类型：</span>
          </div>
          <div class="select">
            <input
              type="text"
              class="input"
              v-model="input.valueEventType"
              style="background:white"
              placeholder="请输入事件类型"
            />
          </div>
        </div>
        <div class="flexContainer">
          <div class="titleselect">
            <span>接收人：</span>
          </div>
          <div class="select" id="classPicPerson" @click="selectDataPicPerson">
            <input
              type="text"
              class="input"
              v-model="input.valuePerson"
              style="background:white"
              disabled
              placeholder="请输入事件接收人"
            />
          </div>
        </div>
      </div>
      <div class="containerMain">
        <div>
          <span>事件内容：</span>
        </div>
        <div>
          <textarea
            name
            id="textvalueMain"
            v-model="textvalueMain"
            cols="40"
            rows="10"
            placeholder="简要说明事件的内容"
          ></textarea>
        </div>
      </div>
      <div class="containerMain" style="margin-bottom:20px;">
        <div>
          <span>备注：</span>
        </div>
        <div>
          <textarea
            name
            id="textvalueMain"
            v-model="textvalueMainremark"
            cols="40"
            rows="10"
            placeholder="如有补充请在此处备注说明"
          ></textarea>
        </div>
      </div>
      <div class="cont">
        <el-upload
          ref="upload"
          action="http://192.168.50.152:8802/job/event"
          multiple
          name="files"
          list-type="picture-card"
          :on-change="handleRemove"
          :http-request="uploadFile"
          :auto-upload="false"
          :limit="4"
          :on-exceed="limitStatus"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" size="tiny">
          <img width="100%" :src="dialogImageUrl" alt />
        </el-dialog>
      </div>
      <!-- <div style="background:white" @click="()=>{
         show=true
      }">
        <span>相机</span>
      </div>-->
      <div class="containerMain">
        <div class="flexContainer">
          <div class="titleselect">
            <span>发起人：</span>
          </div>
          <div class="select">
            <input
              type="text"
              name
              class="input"
              v-model="input.valuesendPerson"
              placeholder="请输入发起人名字"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- <mu-col span="12" style="margin-top:20px;">
      <mu-button @click="confirmSubmit()" full-width class="backgroundNaviBar">提交</mu-button>
    </mu-col>-->
    <div style="padding:10px;">
      <div @click="confirmSubmit()" class="backgroundNaviBar borderSubmit">
        <span>提交</span>
      </div>
    </div>

    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>时间筛选</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="datetime"
            :min-date="minDate"
            :max-date="maxDate"
            @confirm="timeConfirm"
            @cancel="close"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <van-action-sheet
      v-model="show"
      :actions="actions"
      cancel-text="取消"
      @cancel="onCancel"
      @select="selectConfirm"
    />
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);

import { Dialog } from "vant";
import "vant/lib/dialog/style";
import Camera from "../../../static/js/cordovaCamare/camera.js";
export default {
  name: "maintenanceChild3",
  data() {
    return {
      loading: false,
      citys: [],
      filterable: { value1: "" },
      token: "",
      textvalueMainremark: "",
      content: {},
      textvalueMain: "",
      input: {
        valuePerson: "",
        valueEventType: "",
        valueEventName: "",
        valueAddress: "",

        valuesendPerson: ""
      },
      dialogImageUrl: "",
      dialogVisible: false,
      options: ["沙井一期进水口水质监测站"],
      value3: null,
      normal: {
        value1: null,
        value2: null
      },
      checkbox: {
        value1: [],
        value2: false,
        value3: false
      },
      arrPerson: ["周瑞斌", "谢子琪"],

      checkAll: true,
      open: false,
      minDate: new Date("1970-07-01"),
      maxDate: new Date(2050, 10, 1),
      currentDate: new Date(),
      openCamera: false,
      actions: [{ name: "手机拍照" }, { name: "本地相册" }],
      show: false
    };
  },
  methods: {
    confirmSubmit() {
      var _this = this;
      Dialog.confirm({
        title: "请确认是否提交数据？",
        message: "提交用户为：" + sessionStorage.getItem("username")
      })
        .then(() => {
          // on confirm
          _this.submit();
        })
        .catch(() => {
          // on cancel
          Toast("您点击了取消");
        });
    },
    selectDataPicStation() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.input.valueAddress = selectArr[0];
          // _this.open = true;
        }
      });
    },

    selectDataPicPerson() {
      // this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicPerson, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.arrPerson, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择接收人", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.input.valuePerson = selectArr[0];
          // _this.open = true;
        }
      });
    },

    selectConfirm(value) {
      console.log(value.name);
      switch (value.name) {
        case "手机拍照":
          Camera.cameraTakeEvent(0);
          break;

        case "本地相册":
          Camera.cameraTakeEvent(1);
          break;
      }
    },
    onCancel() {
      this.show = false;
      Toast("cancel");
    },
    closeBottomSheetCamera() {},
    timeConfirm(value) {
      this.value3 = AppBace.filterTime(value);
      this.open = false;
    },
    close() {
      this.open = false;
    },
    closeBottomSheet() {},
    timeSelect() {
      this.open = true;
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    limitStatus() {},

    handleRemove(file, fileList) {
      console.log("filelist", fileList);
    },
    uploadFile(res) {
      // this.formDate = new FormData();
      this.formDate.append("files", res.file);
      console.log(res.file);
    },
    async submit() {
      var _this = this;
      this.fullscreen();

      this.formDate = new FormData();
      this.content = {
        hello: "112121"
      };
      //  console.log(AppBace.filter(this.value3));
      //  debugger
      this.$refs.upload.submit();
      this.formDate.append("address", this.input.valueAddress);
      this.formDate.append("createUser", this.input.valuesendPerson);
      this.formDate.append("eventName", this.input.valueEventName);
      this.formDate.append("remark", this.textvalueMainremark);
      this.formDate.append("type", this.input.valueEventType);
      this.formDate.append("receUser", this.input.valuePerson);
      this.formDate.append("content", this.textvalueMain);
      this.formDate.append("gmtEvent", AppBace.filter(this.value3));
      var url = "/job/event";
      var res = await AppBace.request(url, "POST", this.formDate, this.token);
      if (res.data.message == "success") {
        Toast(res.data.info);
        this.$router.go(-1);
        _this.loading.close();
      } else {
        Toast(res.data.info);
        _this.loading.close();
      }
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.input.valuesendPerson = sessionStorage.getItem("username");

    this.citys = this.$store.state.menu;
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
@import "../../../static/css/maintain.css";
.header {
  position: fixed;
  top: 0;
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px #f1eff2 solid;
}
.flexCenterMainChildTextValue {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
}
#textvalueMain {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
  border: 1px #ccc solid;
  outline: none;
}
.MainList {
  border-bottom: 1px solid rgb(243, 243, 243);
  height: 50px;
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
  /* align-items: center; */
  align-content: center;
  align-items: center;
  padding-top: 10px;
  /* justify-content: center; */
}
.select-control-row {
  padding: 8px 0;
  color: blue;
  display: flex;
  flex-direction: column;
}
.select-control-group {
  margin: 16px 0;
  /* color: blue; */
}
.addImageSize img {
  /* width: 100px; */
}
#inputStyles {
  width: 100%;
  outline: none;
  border: none;
  /* border-bottom: 1px solid #ccc; */
}
.inputStyles {
  border: #ccc 1px solid;
  outline: none;
  border-radius: 5px;
}

/* add */
.contanerMain {
  /* margin: 0 5px; */
  background: white;
  padding: 10px;
  /* border-radius: 10px; */
  margin-top: 53px;
  /* margin-bottom: 50px; */
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  border-bottom: 1px #f1eff2 solid;
}
.flexCenterMainChildTextValue {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
}
#textvalueMain {
  height: 80px;
  width: 100%;
  border: none;
  border-radius: 5px;
  border: 1px solid #ccc;
  outline: none;
}
.MainList {
  border-bottom: 1px solid rgb(243, 243, 243);
  height: 40px;
  display: flex;
  /* align-items: center; */
  padding-top: 9px;
  align-content: center;
  /* justify-content: center; */
}
.select-control-row {
  padding: 8px 0;
  color: blue;
  display: flex;
  flex-direction: column;
}
.select-control-group {
  margin: 16px 0;
  /* color: blue; */
}
#input {
  /* widows: 100%; */
  width: 100%;
  outline: none;
  border: none;
  border-radius: 5px;
  height: 30px;
}
.cont {
  padding: 10px;
  border-radius: 10px;
  background: white;
  /* margin-top: 10px; */
}
.bottomBorder {
  border-bottom: 1px solid rgb(241, 241, 241);
}
.borderRad {
  border-radius: 10px;
}
.borderSubmit {
  color: white;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}
</style>



